.container-fluid {
    width: 100%;
    padding-right: 0px;
    padding-left: 0px;
    margin-right: auto;
    margin-left: auto;
    overflow-x: hidden;
}

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 20px;
    padding-left: 20px;
}

.logo {
    font-size: 30px;
    color: #fff;
}

ul {
    list-style: none;
}

nav ul li {
    display: inline-block;
    margin-right: 8px;
    text-transform: uppercase;
    font-weight: 700;
}

nav ul li a {
    text-decoration: none;
    color: #fff;
}


nav ul li a:hover:after {
    background: #fff;
    width: 100%;
}

nav ul li a:after {
    content: '';
    height: 3px;
    -webkit-transition: width .5s ease, background-color .5s ease;
    transition: width .5s ease, background-color .5s ease;
    display: block;
    width: 100%;
    margin: 10px auto auto;
}

nav {
    display: flex;
