}

dl,
ol,
ul {
    margin-top: 14px;
    margin-bottom: 8px;
}

.navbar {
    padding: 0.5rem 1rem;
    display: grid;
    grid-template-columns: auto 1fr;
    -webkit-transition: all .5s ease-out;
    transition: all .5s ease-out;
    position: fixed;
    width: 100% !important;
    left: 0;
    top: 0;
    background-color: #1D81AC;
    border: 1px solid #1D81AC;
    box-shadow: 13px 0px 13px 0px;
    z-index: 1;
}

.navbar button {
    background-color: transparent;
    border: none;
    -webkit-transition: all .5s ease-out;
    transition: all .5s ease-out;
    padding: 5px;
    cursor: pointer;
}

.navbar button:hover {
    background-color: #1D81F5;
    border-radius: 7px;
}



.nav-link {
    color: gray;
    text-transform: capitalize;
    text-decoration: none;
    font-size: 1.2rem;
    -webkit-transition: all 0.3s linear;
    transition: all 0.3s linear;
}

.nav-links {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 20px;
    padding-left: 20px;
}

.nav-links a:hover {
    color: #1D81F5
}

.active {
    color: #6ab04a;
}

@-webkit-keyframes appear {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes appear {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@-webkit-keyframes slideIn {
    0% {
        -webkit-transform: translateX(-2%);
                transform: translateX(-2%);
    }

    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@keyframes slideIn {
    0% {
        -webkit-transform: translateX(-2%);
                transform: translateX(-2%);
    }

    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@-webkit-keyframes shrink {
    0% {
        width: 95%;
    }

    100% {
        width: 90%;
    }
}

@keyframes shrink {
    0% {
        width: 95%;
    }

    100% {
        width: 90%;
    }
}


/*sds*/

.side-nav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #111;
    opacity: 0.9;
    overflow-x: hidden;
    padding-top: 60px;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.side-nav a {
    padding: 10px 10px 10px 30px;
    text-decoration: none;
    font-size: 22px;
    color: #ccc;
    display: block;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

.side-nav a:hover {
    color: #1D81F5;
}

.side-nav .btn-close {
    position: absolute;
    top: 0;
    right: 22px;
    font-size: 36px;
    margin-left: 50px;
}

.open-slide path:hover {
    color: #6ab04a;
}

#main {
    -webkit-transition: margin-left 0.5s;
    transition: margin-left 0.5s;
    padding: 20px;
    overflow: hidden;
    width: 100%;
}

