.containerBox .box {
    position: relative;
    height: 400px;
    width: 300px;
    background: #030303;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #000;
}

.containerBox .box:before {
    content: '';
    position: absolute;
    top: -2px;
    left: -2px;
    right: -2px;
    bottom: -2px;
    background: #fff;
    -webkit-transform: skew(2deg, 2deg);
            transform: skew(2deg, 2deg);
    z-index: -1;
}

.content {
    position: relative;
    padding: 20px;
    -webkit-transform: translateY(40px);
            transform: translateY(40px);
}

.box .content i{
    position: absolute;
    top: -60px;
    right: 20px;
    margin: 0;
    padding: 0;
    font-size: 5em;
    color: rgba(55, 255, 60, 0.15);
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.box:hover .content i {
    color: rgba(55, 255, 60, 0.3);
}

.box .content h3{
    margin: 0 0 10px;;
    padding: 0;
    font-weight: 700;
    color: wheat;

}

.box .content p {
    margin: 0;
    padding: 0;
    color: #fff;
    font-size: 16px;
}

.box .content a{
    position: relative;
    margin: 20px 0 0;
    padding: 10px 20px;
    text-decoration: none;
    border: 1px solid #fff;
    display: inline-block;
    color: #fff;
    -webkit-transition: .5s;
    transition: .5s;
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
    opacity: 0;
    visibility: hidden;
}

.box:hover .content a{
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
    visibility: visible;
}

.box .content a:hover{
    color: #000;
    background: #fff;
}

